.InfoBody{
    width:100%;
    height:100%;
    -webkit-box-shadow: inset 0 0 0 1000px rgba(168, 19, 69, 0.9);
            box-shadow: inset 0 0 0 1000px rgba(168, 19, 69, 0.9);
    font-family: 'Berkshire Swash', cursive; 
}
.AboutContent{
    padding-top: 3em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding-bottom: 2em;
}
.AboutTitle{
    font-size: 2.5em;
}
table{
    width: 85%;
    border-collapse: collapse;
}
th{
    text-align: center;
    border-top: 0.23em solid rgba(29, 25, 32, 0.99);
    width: 50%;
    font-size: 2em;
}
td{
    text-align: left;
    border-top: 0.23em solid rgba(29, 25, 32, 0.99);
    width: 50%;
    padding: 1.2em;
    font-size: 1.5em;
}


